import React, { Component } from "react";
import PropTypes from "prop-types";
import "./SignUpForm.css";
const LEVEL = {
  junior: "junior",
  middle: "middle",
  senior: "senior",
};
const STATE = {
  login: "",
  password: "",
  email: "",
  agreed: false,
  level: null,
};

class SignUpForm extends Component {
  state = {
    ...STATE,
  };

  // * –û—Ç–≤–µ—á–∞–µ—Ç –∑–∞ –æ–±–Ω–æ–≤–ª–µ–Ω–∏–µ —Å–æ—Å—Ç–æ—è–Ω–∏—è
  handleChange = ({ target }) => {
    const { name, value, checked, type } = target;
    // –ï—Å–ª–∏ —Ç–∏–ø —ç–ª–µ–º–µ–Ω—Ç–∞ checkbox, –±–µ—Ä–µ–º –∑–Ω–∞—á–µ–Ω–∏–µ checked,
    // –≤ –ø—Ä–æ—Ç–∏–≤–Ω–æ–º —Å–ª—É—á–∞–µ value
    this.setState({ [name]: type === "checkbox" ? checked : value });
    console.log(this.state.checked);
  };

  // * –í—ã–∑—ã–≤–∞–µ—Ç—Å—è –ø—Ä–∏ –æ—Ç–ø—Ä–∞–≤–∫–µ —Ñ–æ—Ä–º—ã
  handleSubmit = (evt) => {
    evt.preventDefault();
    console.log(`Signed up as: ${this.state.login}`);
    console.log(this.state);
    // –ü—Ä–æ–ø –∫–æ—Ç–æ—Ä—ã–π –ø–µ—Ä–µ–¥–∞–µ—Ç—Å—è —Ñ–æ—Ä–º–µ –¥–ª—è –≤—ã–∑–æ–≤–∞ –ø—Ä–∏ —Å–∞–±–º–∏—Ç–µ
    this.props.onSubmit({ ...this.state });
    this.props.onClose();
    this.setState({ ...STATE });
  };

  render() {
    const { login, password, email, agreed, level } = this.state;

    return (
      <form onSubmit={this.handleSubmit}>
        <label>
          Name
          <input
            type="text"
            placeholder="Enter login"
            name="login"
            value={login}
            onChange={this.handleChange}
          />
        </label>
        <label>
          Email
          <input
            type="email"
            placeholder="Enter email"
            name="email"
            value={email}
            onChange={this.handleChange}
          />
        </label>
        <label>
          Password
          <input
            type="password"
            placeholder="Enter password"
            name="password"
            value={password}
            onChange={this.handleChange}
          />
        </label>

        <section>
          <h2>Choose your gender</h2>
          <label>
            Junior
            <input
              type="radio"
              checked={level === LEVEL.junior}
              name="level"
              value={LEVEL.junior}
              onChange={this.handleChange}
            />
          </label>
          <label>
            Middle
            <input
              type="radio"
              checked={level === LEVEL.middle}
              name="level"
              value={LEVEL.middle}
              onChange={this.handleChange}
            />
          </label>
          <label>
            Senior
            <input
              type="radio"
              checked={level === LEVEL.senior}
              name="level"
              value={LEVEL.senior}
              onChange={this.handleChange}
            />
          </label>
        </section>

        <label>
          Agree to terms
          <input
            name="agreed"
            type="checkbox"
            checked={agreed}
            onChange={this.handleChange}
          />
        </label>
        <button type="submit" disabled={!agreed} onClick={this.handleChange}>
          Sign up as <b>{login}</b>
        </button>
      </form>
    );
  }
}
export default SignUpForm;
